import React, { Component } from "react";
import "./App.css";
import { CardList } from "./component/card-list/card-list.component.jsx";
import { SearchBox } from "./component/search-box.component/search-box.component";

class App extends Component {
  constructor() {
    super();

    this.state = {
      greeting: "Hello Katie!!",
      monsters: [],
      searchField: "",
    };
  }

  componentDidMount() {
    fetch("https://jsonplaceholder.typicode.com/users")
      .then((response) => response.json())
      .then((users) => this.setState({ monsters: users }));
  }

  handleChange = (e) => {
    this.setState({ searchField: e.target.value }, () =>
      console.log(this.state)
    );
  };

  render() {
    const { monsters, searchField } = this.state;
    const filteredMonsters = monsters.filter((monster) =>
      monster.name.toLowerCase().includes(searchField.toLocaleLowerCase())
    );
    return (
      <div className="App">
        {/*<header className="App-header">
          <p>{this.state.greeting}</p>
          <button onClick={() => this.setState({ greeting: "Bye Kattie" })}>
            Click Me!
          </button>
    </header> */}

        <h1>Monsters Rolodex</h1>

        <SearchBox
          placeholder="Search monster here"
          handleChange={this.handleChange}
        />

        <CardList monsters={filteredMonsters}></CardList>
      </div>
    );
  }
}

export default App;
